// @ts-nocheck
// @ts-ignore
import router from "../router/index";
import api from "../../api/api";
const state = {
  clubData: [],
  allClubs: [],
  studentsByClub: [],
};

const getters = {};
const actions = {
  async getClubData({ commit }) {
    api
      .get("/club/getmyclub/")
      .then((res) => {
        commit("SET_CLUB_DATA", res.data);
      })
      .catch((error) => {
        console.log(error);
      });
  },
  async registerClubs({ commit }, clubdata) {
    console.log(clubdata);
    api
      .post("/club/registerclub", clubdata)
      .then((res) => {
        commit("SET_CLUB_DATA", res.data);
        router.push("/clubadmin");
      })
      .catch((error) => {
        console.log(error);
      });
  },
  async getAllClubs({ commit }) {
    api
      .get("/club/")
      .then((res) => {
        commit("SET_ALL_CLUBS", res.data);
      })
      .catch((error) => {
        console.log(error);
      });
  },
  async getStudentsByclubID({ commit }, id) {
    console.log(id, "clubID-getStudentByClibID");
    api.get(`/club/students`, { params: { clubId: id } }).then((res) => {
      commit("SET_ALL_STUDENTS_BY_CLUB_ID", res.data.data);
    });
  },
  async postparticepents({ commit }, { particepents, eventId }) {
    console.log(particepents);
    api
      .post(`/club/events/${eventId}`, particepents)
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
  },
};

const mutations = {
  ["SET_CLUB_DATA"](state, myclub) {
    // state.clubData = null;
    console.log(myclub, "SET_CLUB_DATA");
    state.clubData = myclub.data;
  },
  ["SET_ALL_CLUBS"](state, data) {
    console.log(data);
    state.allClubs = data;
  },
  ["SET_ALL_STUDENTS_BY_CLUB_ID"](state, data) {
    state.studentsByClub = data;
  },
};
export default {
  state,
  getters,
  actions,
  mutations,
};
