<template>
  <div class="relative bg-gray-100 shadow-lg rounded py-6">
    <div
      class="absolute top-0 right-0 rounded-full bg-red-600 w-6 h-6 m-6"
      @click="closeCreateDilouge"
    >
      <svg width="24" xmlns="http://www.w3.org/2000/svg">
        <path
          d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z"
          fill="#fff"
        />
      </svg>
    </div>

    <div class="lg:flex xl:flex mb:flex">
      <div class="px-8">
        <div class="text-2xl font-light text-gray-800">Enter Event Details</div>
        <div class="flex flex-col mb-5">
          <label for="title" class="text-gray-800 pb-2 text-base font-medium">Title</label>
          <input
            type="text"
            class="py-2 bg-gray-300 border-2 border-gray-400 rounded-sm text-gray-800 px-2"
            placeholder="Enter Event title"
            v-model="eventdata.title"
          />
        </div>
        <div class="flex flex-col mb-5">
          <label for="title" class="text-gray-800 pb-2 text-base font-medium">Location</label>
          <input
            type="text"
            class="py-2 bg-gray-300 border-2 border-gray-400 rounded-sm text-gray-800 px-2"
            placeholder="Address for the events"
            v-model="eventdata.location"
          />
        </div>
        <div class="flex flex-col mb-5">
          <label for="title" class="text-gray-800 pb-2 text-base font-medium">Event Date and Time</label>
          <div class="grid grid-cols-2 gap-2">
            <input
              type="Date"
              class="py-2 bg-gray-300 border-2 border-gray-400 rounded-sm text-gray-800 px-2"
              v-model="eventdata.eventDate"
            />
            <input
              type="Time"
              class="py-2 bg-gray-300 border-2 border-gray-400 rounded-sm text-gray-800 px-2"
              v-model="eventdata.reportingTime"
            />
          </div>
          <div class="flex flex-col mb-5">
            <label for="title" class="text-gray-800 pb-2 text-base font-medium">Registration Due</label>
            <div class="grid grid-cols-2 gap-2">
              <input
                type="Date"
                class="py-2 bg-gray-300 border-2 border-gray-400 rounded-sm text-gray-800 px-2"
                v-model="eventdata.registartionDue"
              />
            </div>
          </div>
        </div>
      </div>

      <div>
        <div class="px-8">
          <div class="text-2xl font-light text-gray-800 mx-3">Select Sheet</div>
          <div class="flex py-4 px-2">
            <button
              class="button-p mx-1 text-sm btn-primary"
              @click="fetchSheetData('LEVEL-1')"
            >LEVEL-01</button>
            <button class="button-p mx-1 btn-primary" @click="fetchSheetData('LEVEL-2')">LEVEL-02</button>
            <button class="button-p mx-1 btn-primary" @click="fetchSheetData('LEVEL-3')">LEVEL-03</button>
            <button class="button-p mx-1 btn-primary" @click="fetchSheetData('LEVEL-4')">LEVEL-04</button>
            <button class="button-p mx-1 btn-primary" @click="fetchSheetData('LEVEL-5')">LEVEL-05</button>
          </div>
          <div v-if="!retriveSheetByLevel">
            <div class="text-center text-2xl text-gray-700 py-12">Select Any Level to Retrive Sheets</div>
          </div>
          <div v-else class="px-2">
            <div v-for="level in retriveSheetByLevel" :key="level._id">
              <div
                class="flex justify-between px-6 py-3 my-2 text-gray-800 font-medium"
                :class="
                      eventdata.levelSheets.includes(level._id)
                        ? 'bg-green-300'
                        : 'bg-gray-400'
                    "
                @click="
                      eventdata.levelSheets.includes(level._id)
                        ? removeSheet(level._id)
                        : pushSheet(level._id)
                    "
              >
                <div class="uppercase">{{ level.SheetName }}</div>
                <div class="uppercase">{{ level.sheetLevel }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="w-full flex justify-center">
      <button
        class="bg-blue-500 button-p font-medium text-gray-200 uppercase shadow-lg rounded-sm hover:bg-blue-600 w-64 mx-auto"
        @click="createEvent(eventdata)"
      >Sumbit</button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "EventCreateDialouge",
  data: () => {
    return {
      eventdata: {
        title: "",
        discription: "",
        location: "",
        registartionDue: "",
        eventDate: "",
        reportingTime: "",
        level: "",
        levelSheets: []
      }
    };
  },
  methods: {
    ...mapActions(["getSheetByLevel", "postEventDetails", "createEvent"]),
    log() {
      console.log(this.$data.eventdata);
    },
    pushSheet(id) {
      if (!this.$data.eventdata.levelSheets.includes(id)) {
        this.$data.eventdata.levelSheets.push(id);
      }
    },
    removeSheet(id) {
      for (var i = this.$data.eventdata.levelSheets.length - 1; i >= 0; i--) {
        if (this.$data.eventdata.levelSheets[i] === id) {
          this.$data.eventdata.levelSheets.splice(i, 1);
        }
      }
    },
    fetchSheetData(level) {
      this.eventdata.level = level;
      this.getSheetByLevel(level);
      console.log(level, "seting the level ");
      this.eventdata.levelSheets = [];
    },
    closeCreateDilouge() {
      console.log("close Clicked");
      this.$store.dispatch("getEvents");
      this.$emit("close");
    }
  },
  computed: {
    ...mapGetters(["retriveSheetByLevel"])
  }
};
</script>

