// @ts-nocheck
// @ts-ignore

import api from "../../api/api";
const state = {
  sheetData: {},
  sheetByLevel: null,
};

const getters = {
  retriveSheetByLevel: (state) => {
    return state.sheetByLevel;
  },
};
const actions = {
  async createMarkingSheet({ commit }, markingSheetData) {
    api
      .post("/sheet/createsheet", markingSheetData)
      .then((req) => {
        console.log(req.data);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  async getSheetByLevel({ commit }, level) {
    api
      .get("/sheet/getsheetsbylevel", { params: { level: `${level}` } })
      .then((res) => {
        // console.log(res.data.result);
        commit("SET_SHEET_BY_LEVEL", res.data.result);
      })
      .catch((er) => {
        console.log(er);
      });
  },
};

const mutations = {
  ["SET_SHEET_BY_LEVEL"](state, sheetByLevels) {
    // console.log("SET_SHEET_BY_LEVEL ", state.sheetByLevel);
    // state.sheetByLevel = null;
    // console.log(state.sheetByLevel);
    state.sheetByLevel = sheetByLevels;
  },
};
export default {
  state,
  getters,
  actions,
  mutations,
};
