<template>
  <div>
    <div class="grid grid-cols-3 border-gray-200">
      <div class="flex items-center justify-center">
        <img
          src="https://avatars3.githubusercontent.com/u/21261559?s=400&u=1cc9371cf65523dce7dccffcd3824d26db4ee867&v=4"
          class="h-10 w-10 rounded-full"
        />
      </div>
      <div>
        <div class="text-sm font-semibold text-gray-100">{{getName.name}}</div>
        <div class="text-sm font-bold text-gray-400">{{getName.role}}</div>
      </div>
      <div @click="logOut">Logout</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "usercase",
  mounted() {
    console.log("created");
    this.$store.dispatch("getMe");
  },
  methods: {
    logOut() {
      this.$store.dispatch("logout");
    }
  },

  computed: {
    getName() {
      console.log(this.$store.state.auth.userData);
      if (this.$store.state.auth.userData) {
        return {
          name: this.$store.state.auth.userData.data.name,
          role: this.$store.state.auth.userData.data.role
        };
      }
      return {
        name: "Loading...",
        role: "Loading..."
      };
    }
  }
};
</script>

