// @ts-nocheck
// @ts-ignore
import api from "../../api/api";
const state = {
  currentClub: null,
  eventDataById: null,
};

const getters = {};
const actions = {
  async getClubById({ commit }, id) {
    console.log(id);
    api.get(`/admin/club/${id}`, {}).then((req) => {
      commit("SET_CURRENT_CLUB", req.data);
    });
  },
  async changeClubStatus({ commit }, id) {
    console.log(id);

    api
      .patch(`/admin/club/${id}`)
      .then((res) => {
        commit("SET_CURRENT_CLUB", res.data);
      })
      .catch((error) => {
        console.log(error);
      });
  },
  async getEventDatabyId({ commit }, id) {
    api
      .get(`/admin/event/${id}`, {
        params: {
          options: ["participant"],
        },
      })
      .then((res) => {
        commit("SET_EVENT_BY_ID", res.data);
      })
      .catch((error) => {
        console.log(error);
      });
  },
};

const mutations = {
  ["SET_CURRENT_CLUB"](state, data) {
    this.currentClub = null;
    console.log(data, "SER_CURRENT_CLUB");
    state.currentClub = data;
  },
  ["SET_EVENT_BY_ID"](state, data) {
    console.log(data);
    state.eventDataById = data;
  },
};
export default {
  state,
  getters,
  actions,
  mutations,
};
