<template>
  <div class="p-6">
    <div v-if="!this.$store.state.clubs.clubData">
      <div class="mx-auto mx-4">
        <div
          class="mt-12 bg-red-500 px-2 py-2 rounded-full text-gray-100 text-sm text-center"
        >No Club Register With This Account, Please Enter Club Detail</div>
        <RegisterClub />
      </div>
    </div>
    <div v-else>
      <div class="space-y-2">
        <div class="uppercase text-gray-700 text-2xl font-normal">Club DashBoard</div>
        <div class="xl:w-2/6 lg:w-2/6 p-3">
          <div class="border border-4 w-64 p-2 w-auto">
            <div>
              <div class="text-gray-800 font-medium uppercase">{{clubData.name}}</div>
              <div></div>
            </div>
          </div>
          <div class="pt-6">
            <div class="text-base font-semibold font-normal text-gray-800 uppercase">Current Events</div>
            <div v-for="(event,index) in currentEvents.event" :key="index">
              <router-link :to="{name:'clubadmin/event',
            params:{id:event._id}}">
                <div class="border mt-2">
                  <div
                    class="bg-gray-300 px-3 py-2 text-gray-800 text-lg flex justify-between items-center"
                  >
                    <div>{{event.title}}</div>
                    <div
                      class="text-xs font-medium text-white bg-green-600 px-2 rounded-full"
                    >{{event.level}}</div>
                  </div>
                  <div class="px-6">
                    <div>as</div>
                  </div>
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import RegisterClub from "./RegisterClub";
export default {
  name: "myclub",
  components: {
    RegisterClub
  },
  mounted() {
    this.$store.dispatch("getClubData");
    this.$store.dispatch("getEvents");
  },
  computed: {
    clubData() {
      return {
        name: this.$store.state.clubs.clubData.clubname,
        address: this.$store.state.clubs.clubData.address,
        // admin: this.$store.state.userData.data.name,
        status: this.$store.state.clubs.clubData.approved
      };
    },
    currentEvents() {
      return {
        event: this.$store.state.events.currentEvent
      };
    }
  }
};
</script>

