// @ts-nocheck
// @ts-ignore

import api from "../../api/api";
const state = {
  currentEvent: [],
  eventDetailbyid: null,
};

const getters = {
  getCurrentEvent() {
    return state.currentEvent;
  },
};
const actions = {
  async createEvent({ commit }, eventData) {
    api
      .post("/event/createevent", eventData)
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  async getEvents({ commit }) {
    api
      .get("/event/getcurentevents")
      .then((res) => {
        commit("SET_CURRENT_EVENTS", res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  async getEventById({ commit }, id) {
    api.get(`/event/${id}`).then((res) => {
      commit("SET_EVENT_DETAILS_BY_ID", res.data.data);
    });
  },
};

const mutations = {
  ["SET_CURRENT_EVENTS"](state, currentEvent) {
    state.currentEvent = currentEvent;
  },
  ["SET_EVENT_DETAILS_BY_ID"](state, eventDetails) {
    state.eventDetailbyid = eventDetails;
  },
};
export default {
  state,
  getters,
  actions,
  mutations,
};
