<template>
  <div class="border">
    <div v-for="student in studentList" :key="student._id" class="my-2">
      <div class="bg-white flex px-3 py-2 items-center shadow-xs rounded-sm">
        <div>
          <label class="inline-flex items-center">
            <input type="checkbox" class="form-checkbox" :value="student._id" v-model="participant" />
          </label>
        </div>
        <div>
          <img
            src="https://avatars3.githubusercontent.com/u/21261559?s=400&amp;u=1cc9371cf65523dce7dccffcd3824d26db4ee867&amp;v=4"
            class="h-10 w-10 rounded-full"
          />
        </div>
        <div class="mx-3">
          <div class="text-base text-gray-900">{{student.name}}</div>
          <div class="text-gray-900 text-xs">LEVEL-1</div>
        </div>
      </div>
    </div>
    <button
      @click="postParticepents"
      class="my-2 bg-blue-500 px-2 py-2 font-bold text-white flex rounded-sm uppercase justify-center w-full"
    >Sumbit</button>
  </div>
</template>

<script>
export default {
  name: "SelectStudents",
  props: {
    eventId: String
  },
  data: () => {
    return {
      participant: []
    };
  },
  mounted() {
    this.$store.dispatch(
      "getStudentsByclubID",
      this.$store.state.clubs.clubData._id
    );
  },
  methods: {
    postParticepents() {
      this.$store.dispatch("postparticepents", {
        particepents: this.participant,
        eventId: this.eventId
      });
    }
  },
  computed: {
    studentList() {
      return this.$store.state.clubs.studentsByClub;
    }
  }
};
</script>

