// @ts-nocheck
// @ts-ignore

import api from "../../api/api";
console.log(api);
import axiso from "axios";
const user = JSON.parse(window.localStorage.getItem("user"));
console.log(user);
import router from "../router/index";
const state = {
  me: JSON.parse(window.localStorage.getItem("user")) || "",
  userData: null,
  status: false,
  clubsName: [],
};

const getters = {
  getErrorStatus() {
    console.log("get error Status execurted");
    return state.status;
  },
};
const actions = {
  getMe({ commit }) {
    api
      .get("auth/me")
      .then((res) => {
        commit("SET_USER_DATA", res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  // @ts-ignore
  login({ commit }, loginData) {
    api
      .post("/auth/login", loginData)
      .then((result) => {
        // @ts-ignore
        window.localStorage.setItem("user", JSON.stringify(result.data));
        window.localStorage.setItem("token", result.data.token);
        console.log(result.data.token);

        commit("SET_LOGIN_DATA", result.data);
        commit("SET_ERROR_STATUS", null);
        router.push(`/${result.data.role}`);
      })
      .catch((error) => {
        console.error(error);
        commit("SET_ERROR_STATUS", error.response.data);
        // commit("SET_ERROR_STATUS", ...error);
      });
  },
  // ! registration
  register({ commit }, registerData) {
    api
      .post("/auth/register", registerData)
      .then((result) => {
        commit("SET_LOGIN_DATA", result.data);
        window.localStorage.setItem("user", JSON.stringify(result.data));

        window.localStorage.setItem("token", result.data.token);

        commit("SET_ERROR_STATUS", null);

        router.push(`/${result.data.role}`);
      })
      .catch((error) => {
        commit("SET_ERROR_STATUS", error.response.data);
        console.log(error);
      });
  },
  logout({ commit }) {
    delete axiso.defaults.headers.common["Authorization"];

    commit("CLEAR_USER_DATA");
  },

  getClubsName({ commit }) {
    api
      .get("/auth/clubsname")
      .then((res) => {
        commit("SET_CLUB_NAME", res.data);
      })
      .catch((error) => {
        commit("SET_ERROR_STATUS", error.response.data);
      });
  },
};

const mutations = {
  ["SET_LOGIN_DATA"](state, data) {
    state.me = data;
  },
  ["SET_USER_DATA"](state, data) {
    console.log(data, "SET_USER_DATA MUTATIONS");
    state.userData = data;
  },
  ["SET_ERROR_STATUS"](state, errorResult) {
    console.log("SET ERROR STTATUS EXECUTED");
    state.status = errorResult;
  },
  ["CLEAR_USER_DATA"](state) {
    state.userData = null;
    state.me = null;
    localStorage.removeItem("user");
    localStorage.removeItem("token");

    router.push("/");
    // location.reload();
  },
  ["SET_CLUB_NAME"](state, data) {
    console.log(data);
    state.clubsName = data;
  },
};
export default {
  state,
  getters,
  actions,
  mutations,
};
